<template>

    <MainBanner />

    <div class="container">
        <article>
            <div class="border-top-default"></div>
            <time>{{ day }}/{{ month+1 }}/{{ year }}</time>
            <h1>Jogo individual ou Bolão? Qual o ideal pra você?</h1>
            <p>
                Quem já jogou na loteria pelo menos uma vez na vida em algum momento já se perguntou: Fazer um jogo simples e talvez receber o prêmio sozinho ou 
                participar do sorteio em um bolão e aumentar as chances de ganhar mas dividir o prêmio caso ganhe? 
            </p>
            <p>
                Vamos usar a Mega-Sena como exemplo; há sempre um balanço fixo entre chance de ganhar e custo da aposta. Para aumentar as chances de ganhar é 
                necessário aumentar o valor investido.
            </p>
            <p>
                Um jogo comum na Mega custa R$3,50, ou seja, uma chance em 50.063.860 de acertar os seis números. Os bolões nas casas lotéricas estão disponíveis 
                a partir de R$10 e aumentam as probabilidades porque existem mais variações de jogos.
            </p>
            <p>
                Então, no final das contas a chance de ganhar é quase sempre muito pequena, sendo assim, o melhor a se fazer é dividir o custo das apostas, participando 
                de um bolão e por sua vez dividir os ganhos, caso aconteçam.
            </p>
            <p>
                E por falar em <b>Mega-Sena</b>, a loteria mais popular no Brasil poderá pagar <b>{{ acumuladaProxConcurso }}</b> para quem cravar as seis dezenas no próximo sorteio do concurso <b>{{ concurso+1 }}</b> 
                que acontecerá no dia {{ dataProxConcurso }} às 20h (horário de Brasília).
            </p>
            <p>
                No mais antigo site de jogos lotéricos do Brasil, o <b><a href="https://investloto.com.br" target="_blank">Investloto</a></b>, você consegue apostar na Mega com a maior facilidade e rapidez, além de aumentar suas chances 
                com os bolões.
            </p>
        </article>
    </div>
</template>

<script>
const today = new Date()

import axios from 'axios'
import MainBanner from '@/components/MainBanner.vue'

export default {
    name: 'AllAricles',
    components: { MainBanner },
    data() {
        return {
            day: today.getDate(),
            month: today.getMonth(),
            year: today.getFullYear(),
            concurso: null,
            data: null,
            dezenas: null,
            premiacoes: null,
            acumulou: null,
            acumuladaProxConcurso: null,
            dataProxConcurso: null,
        }
    },
    mounted() {
        axios.get('https://loteriascaixa-api.herokuapp.com/api/mega-sena/latest')
        .then(response => (this.concurso = response.data.concurso, this.data = response.data.data, this.dezenas = response.data.dezenas, this.premiacoes = response.data.premiacoes, 
        this.acumulou = response.data.acumulou, this.acumuladaProxConcurso = response.data.acumuladaProxConcurso, this.dataProxConcurso = response.data.dataProxConcurso))
    }
}
</script>

